body {
    margin: 0;
    padding: 0;
    font-family: arial, helvetica;
    background: #222831 !important;
    overflow: hidden !important;
}

*,
*::after,
*::before {
    -webkit-user-select: none;
    -webkit-user-drag: none;
    -webkit-app-region: no-drag;
    cursor: default;
}
